:export{mediumScreen:992px;smallScreen:540px}.address-picker{display:grid;grid-auto-rows:1fr;grid-column-gap:1.25rem;grid-row-gap:1.25rem;grid-template-columns:1fr}@media (min-width: 992px){.address-picker{grid-column-gap:2rem;grid-row-gap:2rem;grid-template-columns:1fr 1fr}}.address-picker__address{background-color:#f1f5f5;border:1px transparent solid;display:flex;flex-direction:column;padding:1.75rem;transition:.3s border-color}.address-picker__address:hover{cursor:pointer;border-color:#190c4e}.address-picker__address--add-new{align-items:center;color:#190c4e;font-size:1.125rem;font-weight:600;text-transform:uppercase;display:flex;justify-content:center;text-align:center;transition:.3s background-color,.3s color}.address-picker__address--add-new:hover{background-color:#190c4e;color:#fff}.address-picker__address--add-new:hover svg path{fill:#fff}.address-picker__address--add-new svg{margin-bottom:.5rem}.address-picker__address--add-new svg path{transition:.3s fill}.address-picker__address--selected{border:1px #190c4e solid;outline:1px #190c4e solid}.address-picker__address .address-summary{color:#323232;flex-grow:1}.address-picker__address .c-option{background-color:inherit;font-size:.875rem;margin:auto 0 -1rem -1rem}
