:export{mediumScreen:992px;smallScreen:540px}.address-form button{margin:2rem 0 0 2rem}.address-form .react-select-wrapper{margin-bottom:1rem}.address-form__grid{display:grid;grid-gap:0 1rem}@media (min-width: 992px){.address-form__grid{grid-template-columns:1fr 1fr}}@media (min-width: 540px){.address-form__grid--modal{grid-template-columns:1fr 1fr}}@media (min-width: 540px){.address-form__grid--modal.address-form__grid--full{grid-template-columns:none}}@media (min-width: 992px){.address-form__grid--uneven{grid-template-columns:1fr 3fr;margin-right:9rem}}@media (min-width: 992px){.address-form__grid--full{grid-template-columns:none}}.address-form__copy-address{padding:1rem 0 2rem}.address-form .checkbox__bottom{display:block;margin-top:1.5rem}
