:export{mediumScreen:992px;smallScreen:540px}.checkout-review__back{color:#7d7d7d;display:block;padding-bottom:1.5rem;text-decoration:underline}@media (min-width: 992px){.checkout-review__back{margin-left:3rem}}.checkout-review__content{margin:3rem 0}.checkout-review__content__summary{display:grid;grid-template-columns:1fr;grid-gap:2rem;margin-top:5rem}@media (min-width: 540px){.checkout-review__content__summary{grid-template-columns:1fr 1fr}}@media (min-width: 992px){.checkout-review__content__summary{grid-template-columns:1fr 1fr 1fr 1fr}}.checkout-review__content__summary h4{border-bottom:1px solid #f6f6f6;font-weight:600;margin-bottom:1rem;padding-bottom:1rem;text-transform:uppercase;display:flex;justify-content:space-between}.checkout-review__content__summary p{color:#7d7d7d}.checkout-review__content__thumbnail img{width:50px;height:auto}.checkout-review__content__submit{text-align:right;margin-top:6rem}.checkout-review__summary-copy{display:flex;align-items:center;background-color:#f1f5f5;border-radius:30px;height:2.25rem;justify-content:center;width:2.25rem;align-items:center}.checkout-review__summary-copy:hover{cursor:pointer}@media (min-width: 992px){.checkout-review__summary-copy{height:1.5rem;width:1.5rem}.checkout-review__summary-copy svg{transform:scale(0.7)}}
